#examine {
  width: 100%;
  padding-top: 40px;
  padding-right: 30px;
  box-sizing: border-box;
  position: relative;
  padding-bottom: 80px;
}
#examine .head .deptBox,
#examine .head .timeBox {
  display: inline-block;
}
#examine .head .time {
  display: inline-block;
  border-radius: 4px;
  border: 1px solid #999999;
  color: #333333;
  margin-bottom: 25px;
  margin-right: 57px;
  padding: 2px 4px;
  box-sizing: border-box;
}
#examine .head .label {
  display: inline-block;
}
#examine .head .ant-input {
  border: none;
  color: #333333;
}
#examine .head .ant-input:focus {
  box-shadow: none;
}
#examine .head .dept {
  display: inline-block;
}
#examine .head .dept .ant-select-selection {
  border-radius: 4px;
  border: 1px solid #999999;
}
#examine .type {
  padding-top: 20px;
}
#examine .type .label {
  display: inline-block;
}
#examine .adit {
  display: block;
  width: 160px;
  height: 48px;
  line-height: 48px;
  color: #fff;
  background-color: #F1948E;
  border-radius: 4px;
  text-align: center;
  font-size: 16px;
  margin: 50px 0 25px;
}
#examine .checkType {
  background-color: #ECEFF4;
  padding: 8px 20px;
  box-sizing: border-box;
}
#examine .checkType a {
  background-color: #fff;
  display: inline-block;
  margin-right: 13px;
  color: #333333;
  border: 1px solid #c3c5ce;
  padding: 5px 16px;
}
#examine .checkType .allType {
  padding: 5px 16px;
}
#examine .checkType .noExamineType {
  padding: 5px 10px;
}
#examine .checkType .examineSuccess,
#examine .checkType .examineFail {
  background-color: #fff;
  display: inline-block;
  margin-right: 13px;
  color: #333333;
  border: 1px solid #c3c5ce;
  padding: 5px 16px;
  cursor: pointer;
}
#examine .checkType .link-active {
  background-color: #F06E65;
  color: #fff;
  border: 1px solid #e94035;
}
#examine .checkType .link-active .ant-badge-count {
  background-color: #fff;
  color: #f5222d;
}
#examine .articleList {
  padding: 25px 0;
  width: 100%;
}
#examine .articleList .list {
  padding: 30px 78px 14px 30px;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  background-color: #F9FBFF;
  margin-bottom: 20px;
}
#examine .articleList .list .title {
  max-width: 740px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #1E2226;
  font-size: 24px;
  font-weight: 600;
}
#examine .articleList .list img {
  width: 60px;
  position: absolute;
  right: 10px;
  top: 20px;
}
#examine .articleList .list .list_foot {
  margin-top: 50px;
  align-items: flex-end;
}
#examine .articleList .list .list_foot div {
  margin-right: 40px;
  color: #999999;
}
#examine .articleList .list .list_foot div i {
  margin-right: 10px;
}
#examine .articleList .list .operation {
  position: absolute;
  bottom: 14px;
  right: 23px;
  background-color: #F1948E;
  width: 70px;
  text-align: center;
  line-height: 34px;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
}
#examine .articleList .noData {
  text-align: center;
  margin-top: 20%;
}
#examine .articleList .noData img {
  width: 100px;
}
#examine .articleList .noData p {
  padding-top: 10px;
  color: #999;
}
#examine .pagination {
  display: flex;
  padding: 10px 0;
  align-items: center;
  justify-content: center;
}
#examine .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
#examine .flexBox {
  display: flex;
}
#examine .flexBox .flex {
  flex: 1;
}
#hide {
  display: none;
}
.setStep .replyInfo {
  padding-top: 40px;
}
.setStep textarea {
  resize: none;
  font-size: 14px;
}
.setStep .btn {
  padding-top: 60px;
  text-align: center;
}
.setStep .btn span {
  display: inline-block;
  cursor: pointer;
  width: 112px;
  height: 48px;
  line-height: 48px;
  font-size: 20px;
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 30px;
}
.setStep .btn .active {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
